<script>
import { NoticeBar } from 'vant';

import { postApply, postSubmit } from '@/http/service/goods';
import Header from '@/components/header'
export default {
  components: {
    Header,
    NoticeBar
  },
  data() {
    return {
      logisticsCompanyList: [],
      showLogistics: false,
      modifyFlag: false,

      expressPageInfo: {
        logisticsCode: '',
        logisticsName: '',
        logisticsNo: '',
        mobile: '',
        consignee: '刘能',
        consigneeMobile: '18828823434',
        consigneeAddress: '山东省青岛市即墨区朝海街道黄河三路和顺花园8号楼3单元301'
      }
    }
  },
  created() {
    /**
     * @description  获取页面信息
    */
    postApply({
      orderProductSn: this.$route.query.orderNo
    }).then(res => {
      // 数据转换
      if (res.success) {
        this.logisticsCompanyList = res.data.logisticsCompanyList || []
        this.expressPageInfo = res.data.invoicePost
      }
    })
  },
  methods: {
    onConfirmType (value, index) {
      this.showLogistics = false
      this.expressPageInfo.logisticsCode = this.logisticsCompanyList[index].logisticsCode
      this.expressPageInfo.logisticsName = this.logisticsCompanyList[index].logisticsName
    },

    submit() {
      const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
      if (!this.expressPageInfo.logisticsCode) {
        this.$toast.fail('请选择物流公司')
        return false
      }
      if (this.expressPageInfo.logisticsNo === '') {
        this.$toast.fail('请填写物流单号')
        return false
      }
      if (this.expressPageInfo.mobile === '') {
        this.$toast.fail('请填写联系电话')
        return false
      }
      if (!(this.expressPageInfo.mobile && myreg.test(this.expressPageInfo.mobile))) {
        this.$toast.fail('请填写正确联系电话')
        return false
      }
      postSubmit({
        logisticsCode: this.expressPageInfo.logisticsCode,
        logisticsName: this.expressPageInfo.logisticsName,
        logisticsNo: this.expressPageInfo.logisticsNo,
        mobile: this.expressPageInfo.mobile,
        orderProductSn: this.$route.query.orderNo
      }).then(res => {
        if (res.success) {
          if (Number(this.$route.query.invoiceType) !== 2) {
            this.$dialog.alert({
              title: '您的发票邮寄信息已提交成功！',
              message: '换开发票在商城收到原发票及拒收证明后，5个工作日后寄出。'
            }).then(res => {
              this.$router.go(-1)
            })
          } else {
            this.$toast.success('提交成功')
            this.$router.go(-1)
          }
        } else {
          if (res.errorCode === 'INV0009') {
            this.$dialog.alert({
              message: '本次修改失败，发票邮寄仅可三次'
            })
          } else {
            this.$toast.fail(res.message)
          }
        }
      })
    }
  },
  computed: {
    logisticsCompanyNameList() {
      return this.logisticsCompanyList.map(v => v.logisticsName)
    }
  }
}
</script>

